.top_bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filters {
  display: flex;
  list-style: none;
  user-select: none;
  &__item {
    margin-right: 10px;
    height: 26px;
    cursor: pointer;
    &:last-child {
      margin-right: 0;
    }
  }
}
